import React from "react";
import Button from "./Button";

const StartEmulation = (props) => {
    const {
        startEmulation,
        setStartEmulation,
        winningNumbers,
        additionalNumber,
    } = props;

    const handleStartEmulation = () => {
        if (winningNumbers.length !== 6) {
            alert("Please select 6 Winning Numbers");
        } else if (additionalNumber.length !== 1) {
            alert("Please select 1 Additional Number");
        } else {
            setStartEmulation(!startEmulation);
        }
    };

    return <Button onClick={handleStartEmulation}>Start Emulation</Button>;
};

export default StartEmulation;
