import React, { useState } from "react";
import { CSSTransition } from "react-transition-group";
import Emulation from "../components/Emulation";
import Input from "../components/Input";
import Intro from "../components/Intro";
import Layout from "../components/Layout";

const Home = () => {
    const [winningNumbers, setWinningNumbers] = useState([]);
    const [additionalNumber, setAdditionalNumber] = useState([]);
    const [groupOnePrize, setGroupOnePrize] = useState(5000000);
    const [startEmulation, setStartEmulation] = useState(false);

    return (
        <Layout>
            <CSSTransition
                in={!startEmulation}
                mountOnEnter
                unmountOnExit
                timeout={{
                    appear: 2000,
                    enter: 2000,
                    exit: 0,
                }}
                classNames="emulation"
            >
                <Intro />
            </CSSTransition>

            <CSSTransition
                in={!startEmulation}
                mountOnEnter
                unmountOnExit
                timeout={{
                    appear: 2000,
                    enter: 2000,
                    exit: 0,
                }}
                classNames="emulation"
            >
                <Input
                    winningNumbers={winningNumbers}
                    additionalNumber={additionalNumber}
                    setWinningNumbers={setWinningNumbers}
                    setAdditionalNumber={setAdditionalNumber}
                    startEmulation={startEmulation}
                    setStartEmulation={setStartEmulation}
                />
            </CSSTransition>

            <CSSTransition
                in={startEmulation}
                mountOnEnter
                unmountOnExit
                timeout={{
                    appear: 2000,
                    enter: 2000,
                    exit: 0,
                }}
                classNames="emulation"
            >
                <Emulation
                    winningNumbers={winningNumbers}
                    additionalNumber={additionalNumber}
                    groupOnePrize={groupOnePrize}
                    setStartEmulation={setStartEmulation}
                />
            </CSSTransition>
        </Layout>
    );
};

export default Home;
